#navHeader{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1rem 0;
}

.logo{
  font-size: 2.5rem;
  text-decoration: none;
  font-weight: bold;
  color: var(--clr-main-50);
}

.navMenu>ul{
  display: flex;
  gap: 1rem;
  list-style: none;
}

.navMenu a{
  text-decoration: none;
  color: var(--clr-dk);
}
